import React, { Component } from 'react';
import '../App.css';
import StepperCompo from './Stepper/StepperCompo';

class SelectColor extends Component {
  constructor(props) {
    super(props);
    this.state = {
      colorStore: 'gray',
    };
  }

  continue = (e) => {
    e.preventDefault();
    this.props.nextStep();
  };

  back = (e) => {
    e.preventDefault();
    this.props.prevStep();
  };

  handleColorChange = (e,color) => {
    e.preventDefault();
    this.setState({ colorStore: color }, () => {
      this.props.handleChange('theme_color');
    });
  };

  render() {
    // const {
    //   businessname,
    //   validateBusinessName,
    //   isErrorBusinessName,
    //   errorMessageBusinessName,
    // } = this.props;

    return (
      <div className="form">
        <form>
          <StepperCompo count={1} />
          <div className="form-group__element">
            <div className="Main">
              <button onClick={(e) => { this.handleColorChange(e,'red') }}>
                <div className="red"></div>
              </button>
              <button onClick={(e) => { this.handleColorChange(e,'blue') }}>
                <div className="red blue"></div>
              </button>
              <button onClick={(e) => { this.handleColorChange(e,'green') }}>
                <div className="red green"></div>
              </button>
              <button onClick={(e) => { this.handleColorChange(e,'orange') }}>
                <div className="red orange"></div>
              </button>
            </div>
            <input type="hidden" name="theme_color" value={this.state.colorStore} />
          </div>

          <div className="DivColor">
            <div
              style={{
                backgroundColor: this.state.colorStore,
                height: '40px',
                width: '40px',
                borderRadius: '50%',
              }}
            ></div>
          </div>

          <div className="buttons">
            <button className="buttons__button buttons__button--back" onClick={this.back}>
              Back
            </button>
            <button className="buttons__button buttons__button--next" onClick={this.continue}>
              Next
            </button>
          </div>
        </form>
      </div>
    );
  }
}

export default SelectColor;
