import React, { Component } from 'react';
import '../App.css';
import StepperCompo from './Stepper/StepperCompo';

class BusinessDetails extends Component {
  continue = e => {
    e.preventDefault(); 
    // const isBusinessNameValid = this.props.validateBusinessName();
    // const isBusinessLogoValid = this.props.validateBusinessLogo();
    // if (isBusinessNameValid && isBusinessLogoValid) {
      this.props.nextStep();
    // }
  }
  back = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  render() {
    const { 
      storename, 
      handleChange,
      submitData
    } = this.props;

    return (
      <div className='form'>
        <form>
          <StepperCompo count={5}/>
          <div className='form-group'>
            <div className='form-group__element'>
              <label htmlFor='business name' className='form-group__label'>
              Store Name
              </label>
              <input type='text' value={storename} name='storename' onChange={handleChange('storename')}  className='form-group__input'/>
              
            </div>
          </div>
            
          <div style={{textAlign: 'center'}}>
            <button className='buttons__button buttons__button--back' onClick={this.back}>Back</button>
            <button className='buttons__button buttons__button--next' type='submit' onClick={submitData}>Submit</button>
          </div>

        </form>
      </div>
    )
  }
}

export default BusinessDetails;