import React, { Component } from 'react';
import BusinessDetails from './BusinessDetails';
import ThemeConfig from './themeConfig';
import SelectColor from './SelectColor';
import PickLogo from './PickLogo';
import GiveInstruction from './GiveInstruction';
import StoreInfo from './StoreInfo';
import axios from "axios";




class Form extends Component {
  state = {
    step: 1,
    businessname: '',
    storename: '',
    businesslogo: '',
    email: '',
    theme_color: '',
    product_display: '',
    payment_method: '',
    isErrorBusinessName: true,
    isErrorBusinessLogo: true,
    errorMessageBusinessName: '',
    errorMessageBusinessLogo: '',
    posts : [],
    storeLogo: ''
  };


  nextStep = () => {
    const { step } = this.state;
    this.setState({
      step: step + 1
    })
  }

  prevStep = () => {
    const { step } = this.state;
    this.setState({
      step: step - 1
    })
  }


  handleChange = input => e => {
    const productdisplay = e.target.getAttribute("data-productdisplay");
    const paymentmethod = e.target.getAttribute("data-paymentmethod");
    console.log("==");
    console.log("=="+paymentmethod);

    if (input === 'product_display') {
      this.setState({
        [input]: productdisplay
      })
    }else if (input === 'payment_method') {
      this.setState({
        [input]: paymentmethod
      })
    }else{
      this.setState({
        [input]: e.target.value
      })
    }

    if (input === 'businessname') {
      if (this.state.businessname.length >= 1) {
        this.setState({
            isErrorBusinessName: false
        })
      }
    }

    else if (input === 'businesslogo') {
    //   if (this.state.businesslogo.length >= 1) {
    //     this.setState({
    //         isErrorBusinessLogo: false
    //     })
    //   }
    }
  }

  addColor = e => {
    const colorschema = e.target.value;
    this.setState({
      customcolor: colorschema
    });
  };

//   addCourse = data => {
//     const id = data.map(v => v.id);
//     this.setState({
//       courses: id
//     })
//   };

  validateBusinessName = () => {
    if (this.state.businessname.length < 2) {
      this.setState({
        isErrorBusinessName: true,
        errorMessageBusinessName: 'Type your Business name (at least 2 characters)'
      });
      return false;
    }
    return true;
  }


  onSelectImageHandler = (files) => {
    this.setState({
      file: files[0],
    });
    
  }

  submitData = e => {
    try {
      const formData = new FormData();

      formData.append('file', this.state.file);
      formData.append('businessname',this.state.businessname);
      formData.append('storename',this.state.storename);
      formData.append('email',this.state.email);
      formData.append('theme_color',this.state.theme_color);
      formData.append('product_display',this.state.product_display);
      formData.append('payment_method',this.state.payment_method);
      formData.append('fontchoices',this.state.fontchoices);


      const config = {
          headers: {
              "Contetnt-Type":"multipart/form-data" 
          }
      };

    e.preventDefault();
    axios.post(`${process.env.REACT_APP_FRONTEND_URL}api/create/store`, formData, config ).then((response) => {
      alert(response.data.message);
      this.setState({
        posts: response.data
      })
    });

    } catch (error) {
      console.log(error)
    }
    
  }




  render() {
    const {
      step,
      businessname,
      storename,
      product_display,
      payment_method,
      theme_color,
      email,
      addColor,
      pickLogo,
      isErrorBusinessName,
      errorMessageBusinessName,
    } = this.state;


    
    switch(step) {
      case 1: 
        return (
          <BusinessDetails 
            nextStep={this.nextStep}
            handleChange={this.handleChange}
            businessname={businessname}
            email={email}
            validateBusinessName={this.validateBusinessName}
            isErrorBusinessName={isErrorBusinessName}
            errorMessageBusinessName={errorMessageBusinessName}
          />
        )
      case 2:
        return (
            <SelectColor 
            nextStep={this.nextStep}
            prevStep={this.prevStep}
            handleChange={this.handleChange}
            addColor={addColor}
          />
        )
      case 3:
        return (
            <PickLogo 
            nextStep={this.nextStep}
            prevStep={this.prevStep}
            handleChange={this.handleChange}
            onSelectImageHandler={this.onSelectImageHandler}
          />
        )
      case 4:
        return (
            <ThemeConfig 
            nextStep={this.nextStep}
            email={email}
            prevStep={this.prevStep}
            handleChange={this.handleChange}
            theme_color={theme_color}
            product_display={product_display}
            payment_method={payment_method}
          />
        )
      case 5:
        return (
            <GiveInstruction 
            nextStep={this.nextStep}
            prevStep={this.prevStep}
            handleChange={this.handleChange}
          />
        )
      case 6:
        return (
            <StoreInfo 
            nextStep={this.nextStep}
            prevStep={this.prevStep}
            handleChange={this.handleChange}
            storename={storename}
            submitData={this.submitData}
          />
        )
      default: return null
    }
  }
}

export default Form;
